body::-webkit-scrollbar {
  width: 10px;
  /* Ширина всего элемента навигации */
}

body::-webkit-scrollbar-track {
  background: #434242;
  /* Цвет дорожки */
}

body::-webkit-scrollbar-thumb {
  background-color: #eb65df;
  /* Цвет бегунка */
  border-radius: 20px;
  /* Округление бегунка */
  border: 3px solid #eb65df;
  /* Оформление границ бегунка */
}

.container {
  max-width: 1600px;
  margin-right: auto;
  margin-left: auto;
  padding-right: 10px;
  padding-left: 10px;
}

.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 20px;
  padding-top: 10px;
}
